<template>
    <div id="App">
        <div class="NotifyBox" v-if="notify.length > 0">
            <p>{{notify}}</p>
        </div>

        <div class="BodyBox" v-if="openned">
            <div class="MachineBox">
                <div class="LettersBox">
                    <p class="Letters">A</p>
                    <p class="Letters LettersCenter">B</p>
                    <p class="Letters">C</p>
                </div>

                <div class="LineBox Line1Box">
                    <div
                        class="ProductImage"    
                        v-for="i in 3"
                        :key="'Img'+i+0"
                    >
                    <img
                        v-if="products[i+0-1]"
                        :src="`img/${products[i+0-1].name}.png`"
                    >
                    </div>
                </div>

                <div class="LineBox Line2Box">
                    <div
                        class="ProductImage"    
