                        class="ProductImage"    
                        v-for="i in 3"
                        :key="'Img'+i+6"
                    >
                    <img
                        v-if="products[i+6-1]"
                        :src="`img/${products[i+6-1].name}.png`"
                    >
                    </div>
                </div>

                <div class="LineBox Line4Box">
