                        :key="'Img'+i+9"
                    >
                    <img
                        v-if="products[i+9-1]"
                        :src="`img/${products[i+9-1].name}.png`"
                    >
                    </div>
                </div>

                <div class="LineBox Line5Box">
                    <div
                        class="ProductImage"    
                        v-for="i in 3"
                        :key="'Img'+i+12"
                    >
                    <img
                        v-if="products[i+12-1]"
                        :src="`img/${products[i+12-1].name}.png`"
                    >
                    </div>
                </div>
            </div>

            <div class="PanelBox">
                <div class="PanelMessageBox">
                    <p v-if="!product_selected" class="PanelMessage" :class="{PanelEffect: panel.length == 0}">{{panel == "" ?  translate.TR_SELECT_PRODUCT : `${translate.TR_PRODUCT}: ${panel}`}}</p>
                    <p v-else-if="!confirm" class="PanelMessage">{{`${product_selected.label}`}}<br/>{{`${translate.TR_SIMBOL_MONEY} ${product_selected.price}`}}</p>
                    <p v-else class="PanelMessage">{{translate.TR_CONFIRMING}}</p>
                </div>

                <div class="PanelKeyBox">
                    <p class="PanelKey" @click="clickPanel('A')">A</p>
                    <p class="PanelKey" @click="clickPanel('B')">B</p>
                    <p class="PanelKey" @click="clickPanel('C')">C</p>
                    <p class="PanelKey"
                        v-for="indice in 9"
                        :key="indice"
                        @click="clickPanel(indice)"
                    >{{indice}}</p>
                </div>

                <div class="PanelListOptionsBox">
                    <div class="PanelOptionBox">
                        <div class="ClickBox" @click="confirmPayment"></div>
