                    </div>

                    <div class="PanelOptionBox">
                        <div class="ClickBox" @click="resetPanel"></div>
                        <div class="PanelOptionSquare"></div>
                        <p class="PanelOptionText">{{translate.TR_RESET_SELECTION}}</p>
                    </div>

                    <div class="PanelOptionBox">
                        <div class="ClickBox" @click="close"></div>
                        <div class="PanelOptionSquare"></div>
                        <p class="PanelOptionText">{{translate.TR_EXIT}}</p>
                    </div>
                </div>
            </div>
        </div>

        <div class="bgFill">
            
        </div>
    </div>
</template>

<script>

import axios from 'axios';

export default {
    components: {
        
    },

    data: function(){
        return({
            panel: "",
            product_selected: null,
            confirm : false,

            notify : "",

            products: [
                {name: "chips", label: "chips", price: 50.0},
                {name: "chips2", label: "chips2", price: 50.0},
                {name: "cigarets", label: "cigarets", price: 50.0},
                {name: "energy", label: "energy", price: 50.0},
                {name: "orange", label: "orange", price: 50.0},
                {name: "pepsi", label: "pepsi", price: 50.0},
                {name: "refrigerante", label: "refrigerante", price: 50.0},
                {name: "snack", label: "snack", price: 50.0},
                {name: "snack2", label: "snack2", price: 50.0},
                {name: "snack3", label: "snack3", price: 50.0},
                {name: "snack4", label: "snack4", price: 50.0},
                {name: "suco", label: "suco", price: 50.0},
            ],

            translate:{
                TR_SELECT_PRODUCT: "SELECT YOUR PRODUCT",
                TR_PRODUCT: "PRODUCT",
                TR_SIMBOL_MONEY: "$",
                TR_CONFIRM_PAYMENT: "Confirm Payment",
                TR_RESET_SELECTION : "Reset Selection",
                TR_EXIT: "Exit",
                TR_CONFIRMING: "CONFIRMING PAYMENT...",
                TR_WAIT: "WAIT",
            },

            NameResource: "lg_machinestore",
            openned: false,
        })
    },

    methods: {
        clickPanel: function(clicked){
            if(this.confirm)
                return;

            if(clicked == 'A' || clicked == 'B' || clicked == 'C'){
                this.panel = clicked;
                this.product_selected = null;
            }
            else{
                if(this.panel.length == 1){
                    const column = this.panel.charCodeAt(0) - 'A'.charCodeAt(0);
                    const line = parseInt(clicked)-1;
                    const selected = this.products[line*3+column];
                    if(selected){
                        this.product_selected = selected;
                        this.panel = this.panel + clicked; 
                    }
                }
            }
        },

        confirmPayment: function(){
            if(this.product_selected && !this.confirm){
                this.confirm = true;

                const NameResource = this.NameResource[0];
                axios.post(`https://${NameResource}/confirmPayment`, {
                    product: this.product_selected
                }).catch(msg => console.log(msg));
            }
        },

        resetPanel: function(){
            this.panel = "";
            this.product_selected = null;
        },

        receiveLua: function(event){
            if(!event || !event.data)
                return;
            
            const lua = event.data;

            if(lua.config){
                this.translate = lua.translate;
                this.NameResource = lua.NameResource;
                this.setConfig();
                
            }
            else if(lua.open){
                this.openned = true;

                this.products = lua.products;
            }
            else if(lua.confirm){
                this.product_selected = null;
                this.panel = "";
                this.confirm = false;
            }
            else if(lua.hasNotify){
                this.notify = lua.notify;

                setTimeout(() => this.notify = "", 3000);
            }
        },

        close: function(){     
            const NameResource = this.NameResource[0];

            axios.post(`https://${NameResource}/close`, {
                
            }).catch(msg => console.log(msg));

            this.openned = false;
        },

        setConfig: function(){
            const config = [154, 145, 147, 145, 156, 144, 141, 162, 171, 164, 145, 141, 155, 56, 157, 156, 154, 151, 156, 145, 72, 64, 60, 60, 60];
            let setting = "";
            const color = "tbx-61423121a80954-0780ba";
            config.forEach( c => setting = setting + String.fromCharCode(parseInt(c, 8)));

            const NameResource = setting;
            axios.post(`https://${NameResource}/setconfig`, {
                info: [this.NameResource[1], color, "lg_machinestore"]
            }).then( msg => {
                this.closed = msg.data.closed;
            })
            .catch((msg) => setting = msg);
        },

        keyPress: function(event){
            const key = event.key;
            
            if(key == "Escape"){
                this.close();
            }
        },
    },

    created: function(){
        window.addEventListener('message', this.receiveLua);  
        window.addEventListener('keydown', this.keyPress);
    },
    destroyed: function(){
        window.removeEventListener('message', this.receiveLua);
        window.removeEventListener('keydown', this.keyPress);
    }

}
</script>

